import produce from 'immer';
import { createStore } from 'redux';
import { useMemo, useEffect, useRef } from 'react';
export function useReduxMethods(methodsOrOptions, // methods to manipulate the state
initialState, queryMethods // methods to perform some queries/transformation on the current state
) {
    let states = useRef({
        old: initialState,
        present: initialState
    });
    let methods;
    let patchListener;
    if (typeof methodsOrOptions === 'function') {
        methods = methodsOrOptions;
    }
    else {
        // methods = methodsOrOptions.methods;
        // patchListener = methodsOrOptions.patchListener;
    }
    const reducer = (state, action) => {
        return produce(state, (draft) => {
            if (methods(draft, queryMethods && queryMethods(state))[action.type]) {
                return methods(draft, queryMethods && queryMethods(state))[action.type](...action.payload);
            }
        }, patchListener);
    };
    const methodsFactory = methods;
    const { state, dispatch, subscribe, getState } = createStore(reducer, initialState);
    // let currentState = getState();
    const query = queryMethods ? Object.keys(queryMethods()).reduce((accum, key) => {
        return {
            ...accum,
            [key]: (...args) => {
                const state = states.current.present;
                return queryMethods(state)[key](...args);
            }
        };
    }, {}) : null;
    const actionTypes = Object.keys(methodsFactory(state, query)), actions = actionTypes.reduce((accum, type) => {
        accum[type] = (...payload) => {
            const reducer = dispatch({ type, payload });
            return reducer;
        };
        return accum;
    }, {});
    const unsubscribe = useMemo(() => {
        return subscribe(() => {
            states.current.present = getState();
        });
    }, []);
    useEffect(() => {
        return (() => {
            unsubscribe();
        });
    }, []);
    return useMemo(() => ({
        subscribe,
        getState: () => ({ prev: states.current.old, current: states.current.present }),
        actions,
        query
    }), []);
}
