import { ERROR_INVALID_NODEID, ERROR_ROOT_CANVAS_NO_ID, ROOT_NODE } from "@craftjs/utils";
import { isCanvas, isTopLevelCanvas } from "../nodes";
import { updateEventsNode } from "../utils/updateEventsNode";
const invariant = require('invariant');
const Actions = (state, query) => {
    const _ = (name) => Actions(state, query)[name];
    return {
        setOptions(cb) {
            cb(state.options);
        },
        setIndicator(indicator) {
            if (indicator && (!indicator.placement.parent.dom || (indicator.placement.currentNode && !indicator.placement.currentNode.dom)))
                return;
            state.events.indicator = indicator;
        },
        setNodeEvent(eventType, id) {
            const current = state.events[eventType];
            if (current && id != current) {
                state.nodes[current].events[eventType] = false;
            }
            if (id) {
                const node = state.nodes[id];
                state.nodes[id].events[eventType] = true;
                state.events[eventType] = id;
            }
            else {
                state.events[eventType] = null;
            }
        },
        replaceNodes(nodes) {
            state.nodes = nodes;
        },
        add(nodes, parentId) {
            if (!Array.isArray(nodes))
                nodes = [nodes];
            if (parentId && !state.nodes[parentId].data.nodes && isCanvas(state.nodes[parentId]))
                state.nodes[parentId].data.nodes = [];
            nodes.forEach(node => {
                const parent = parentId ? parentId : node.data.parent, parentNode = state.nodes[parent];
                if (parentNode && isCanvas(node) && !isCanvas(parentNode)) {
                    invariant(node.data.props.id, ERROR_ROOT_CANVAS_NO_ID);
                    if (!parentNode.data._childCanvas)
                        parentNode.data._childCanvas = {};
                    node.data.parent = parentNode.id;
                    parentNode.data._childCanvas[node.data.props.id] = node.id;
                    delete node.data.props.id;
                }
                else {
                    if (parentId)
                        query.canDropInParent(node, parentId);
                    if (parentNode) {
                        if (parentNode.data.props.children)
                            delete parentNode.data.props["children"];
                        // if (parentId && !state.nodes[parentId].data.nodes) state.nodes[parentId].data.nodes = [];
                        if (!parentNode.data.nodes)
                            parentNode.data.nodes = [];
                        const currentNodes = parentNode.data.nodes;
                        currentNodes.splice((node.data.index !== undefined) ? node.data.index : currentNodes.length, 0, node.id);
                        node.data.parent = parent;
                    }
                }
                state.nodes[node.id] = node;
            });
        },
        reset() {
            state.nodes = {};
            state.events = {
                dragged: null,
                selected: null,
                hovered: null,
                indicator: null
            };
        },
        move(targetId, newParentId, index) {
            const targetNode = state.nodes[targetId], newParent = state.nodes[newParentId], newParentNodes = newParent.data.nodes;
            query.canDropInParent(targetNode, newParentId);
            const currentParent = state.nodes[targetNode.data.parent], currentParentNodes = currentParent.data.nodes;
            currentParentNodes[currentParentNodes.indexOf(targetId)] = "marked";
            if (newParentNodes)
                newParentNodes.splice(index, 0, targetId);
            else
                newParent.data.nodes = [targetId];
            state.nodes[targetId].data.parent = newParentId;
            state.nodes[targetId].data.index = index;
            currentParentNodes.splice(currentParentNodes.indexOf("marked"), 1);
            // updateEventsNode(state, targetId);
        },
        delete(id) {
            invariant(id != ROOT_NODE, "Cannot delete Root node");
            const targetNode = state.nodes[id];
            if (isCanvas(targetNode)) {
                invariant(!isTopLevelCanvas(targetNode), "Cannot delete a Canvas that is not a direct child of another Canvas");
                targetNode.data.nodes.map((childId) => {
                    _("delete")(childId);
                });
            }
            const parentNode = state.nodes[targetNode.data.parent];
            if (parentNode && parentNode.data.nodes.indexOf(id) > -1) {
                parentNode.data.nodes.splice(parentNode.data.nodes.indexOf(id), 1);
            }
            updateEventsNode(state, id, true);
            delete state.nodes[id];
        },
        setProp(id, cb) {
            invariant(state.nodes[id], ERROR_INVALID_NODEID);
            cb(state.nodes[id].data.props);
            // updateEventsNode(state, id);
        },
        setDOM(id, dom) {
            invariant(state.nodes[id], ERROR_INVALID_NODEID);
            state.nodes[id].dom = dom;
            // updateEventsNode(state, id);
        },
        setHidden(id, bool) {
            state.nodes[id].data.hidden = bool;
        },
        setCustom(id, cb) {
            cb(state.nodes[id].data.custom);
        }
    };
};
export default Actions;
