import { useReduxMethods } from "@craftjs/utils";
import Actions from "./actions";
import { QueryMethods } from "./query";
export const useEditorStore = (nodes = {}, events = {
    selected: null,
    dragged: null,
    hovered: null,
    indicator: null
}, options = {}) => {
    return useReduxMethods(Actions, {
        nodes,
        events,
        options,
    }, QueryMethods);
};
